.page{
  width: 100%;
  /* height: 100%; */
  opacity: 1;
  left: 0;
}

.page .inner{
  position: relative;
  top: auto;
  left: auto;
  right: auto;
  bottom: auto;
  opacity: 1
}

.page .background{
  opacity: 0;
  background-position: top;
  -webkit-animation: fade-in 1000ms ease-in 1000ms;
          animation: fade-in 1000ms ease-in 1000ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  position: fixed;
  width: 100%;
}
@-webkit-keyframes fade-in{
  0%{opacity: 0;}
  100%{opacity: 1;}
}
@keyframes fade-in{
  0%{opacity: 0;}
  100%{opacity: 1;}
}
.page:hover .background{opacity: auto;}


.header{
  position:absolute;
  top: 32px;
  right: 32px;
  width: 200px;
  white-space: nowrap;
  text-align: right;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-transform: uppercase;
  color: white;
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-animation: header-slide-in 500ms linear 1300ms;
          animation: header-slide-in 500ms linear 1300ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.header:before{
  content: '';
  position: absolute;
  right: 0;
  width: 0px;
  height: 100%;
  background: white;
  -webkit-animation: header-slide-in-out 1000ms linear 1000ms;
          animation: header-slide-in-out 1000ms linear 1000ms;
}

.header h1{
  overflow: hidden;
}

.header h1:after{
  content:'';
  position: absolute;
  bottom: -6px;
  width: 48px;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  height: 3px;
  background: white;
  right:0;
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
  -webkit-animation: header-slide-in 500ms linear 1800ms;
          animation: header-slide-in 500ms linear 1800ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes header-slide-in{
  0%{-webkit-transform: scaleX(0);transform: scaleX(0);}
  100%{-webkit-transform: scaleX(1);transform: scaleX(1);}
}

@keyframes header-slide-in{
  0%{-webkit-transform: scaleX(0);transform: scaleX(0);}
  100%{-webkit-transform: scaleX(1);transform: scaleX(1);}
}

@-webkit-keyframes header-slide-in-out{
  0%{  width:0px;   right: 0;    left: auto;}
  45%{ width:200px; right:0;     left: auto;}
  55%{ width:200px; right: auto; left:0;}
  99%{ width:0px;   right: auto; left: 0;}
  100%{width:0px;   right: 0;    left: auto;}
}

@keyframes header-slide-in-out{
  0%{  width:0px;   right: 0;    left: auto;}
  45%{ width:200px; right:0;     left: auto;}
  55%{ width:200px; right: auto; left:0;}
  99%{ width:0px;   right: auto; left: 0;}
  100%{width:0px;   right: 0;    left: auto;}
}



