#logo-container{
  position: absolute;
  -webkit-animation: swirl 800ms cubic-bezier(0.68, 0.68, 0.265, 1.55) 500ms;
          animation: swirl 800ms cubic-bezier(0.68, 0.68, 0.265, 1.55) 500ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-transition: top 0.6s ease-in, left 0.6s ease-in;
  -o-transition: top 0.6s ease-in, left 0.6s ease-in;
  transition: top 0.6s ease-in, left 0.6s ease-in;
  z-index: 100;
  left: 64px;
  top: 64px;
}

#logo-container.center{
  position: absolute;
  left: 50%;
  top: 30%;
}

#logo-inner-container{
  position: absolute;
  height: 64px;
  width: 64px;
}

#logo{
  position: absolute;
  height: 64px;
  width: 64px;
  margin-left: -50%;
  margin-top: -50%;
}

#logo:hover{
  -webkit-animation: hover 800ms cubic-bezier(0.68, 0.68, 0.265, 1.55);
          animation: hover 800ms cubic-bezier(0.68, 0.68, 0.265, 1.55);
}

@-webkit-keyframes swirl{
  0% {-webkit-transform: rotate(-360deg) scale(0);transform: rotate(-360deg) scale(0);}
  100% {-webkit-transform: rotate(0) scale(1);transform: rotate(0) scale(1);}
}

@keyframes swirl{
  0% {-webkit-transform: rotate(-360deg) scale(0);transform: rotate(-360deg) scale(0);}
  100% {-webkit-transform: rotate(0) scale(1);transform: rotate(0) scale(1);}
}

@-webkit-keyframes hover{
  0%{-webkit-transform: rotate(0) scale(1);transform: rotate(0) scale(1)}
  25%{-webkit-transform: rotate(-8deg) scale(1.05);transform: rotate(-8deg) scale(1.05);}
  50%{-webkit-transform: rotate(8deg) scale(1.1);transform: rotate(8deg) scale(1.1);}
  100%{-webkit-transform: rotate(0) scale(1);transform: rotate(0) scale(1)}
}

@keyframes hover{
  0%{-webkit-transform: rotate(0) scale(1);transform: rotate(0) scale(1)}
  25%{-webkit-transform: rotate(-8deg) scale(1.05);transform: rotate(-8deg) scale(1.05);}
  50%{-webkit-transform: rotate(8deg) scale(1.1);transform: rotate(8deg) scale(1.1);}
  100%{-webkit-transform: rotate(0) scale(1);transform: rotate(0) scale(1)}
}



