.loading-bar-container{
  position: absolute;
  float: right;
  left: 50%;
  z-index: 60;
  -webkit-transition: top 1s ease-in;
  -o-transition: top 1s ease-in;
  transition: top 1s ease-in;
}

.loading-bar-inner{
  margin-left: -50%;
  /* display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-align: center; */
  width: 100%;
}

.loading-bar{
  width: 48px;
  margin-top: 8px;
}

.loading-bar-subtitle{
  text-align: center;
  -ms-flex: 0 0 100%;
      flex: 0 0 100%;
  margin:0;
  -webkit-transform: translateY(12px);
      -ms-transform: translateY(12px);
          transform: translateY(12px);
  opacity: 0;
  -webkit-animation: fade-in-up 0.5s ease-in;
          animation: fade-in-up 0.5s ease-in;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes fade-in-up{100%{-webkit-transform: translateY(0);transform: translateY(0); opacity: 1}}

@keyframes fade-in-up{100%{-webkit-transform: translateY(0);transform: translateY(0); opacity: 1}}

.loading-bar-title{
  position: relative;
  margin:0;
  /* margin-left: 20%; */
  white-space: nowrap;
  text-align: center;
  color: #E65151;
}

.loading-bar-title:before{
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  max-width: 0;
  /* border-bottom: 2px solid #fff; */
  color: #fff;
  content: 'Sean Verhaagen';
  -webkit-animation: loading-name 1s;
          animation: loading-name 1s;
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
  white-space: nowrap;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;

}

@-webkit-keyframes loading-name {
  0%{max-width: 0%;}
  100%{max-width: 100%;}
}

@keyframes loading-name {
  0%{max-width: 0%;}
  100%{max-width: 100%;}
}

/* .loading-bar-title:hover::before{max-width: 100%;} */

.loading-bar-dot{
  width: 8px;
  height: 8px;
  display: inline-block;
  -webkit-animation: bounce 1s linear;
          animation: bounce 1s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  border-radius: 32px;
  margin: 0 4px;
  -webkit-transition: opacity 0.3s ease-out;
  -o-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}

.loading-bar-dot:nth-child(1){-webkit-animation-delay: -0.32s;animation-delay: -0.32s;}
.loading-bar-dot:nth-child(2){-webkit-animation-delay: -0.16s;animation-delay: -0.16s;}

.loading-bar.done .loading-bar-dot{opacity: 0;}

@-webkit-keyframes bounce {
  0%{-webkit-transform: scaleY(1) translateY(0);transform: scaleY(1) translateY(0);}
  10%{-webkit-transform: scaleY(0.3) translateY(3px);transform: scaleY(0.3) translateY(3px);}
  30%{-webkit-transform: scaleY(1);transform: scaleY(1)}
  30%{-webkit-transform: scaleY(1.3) translateY(-16px);transform: scaleY(1.3) translateY(-16px);}
  60%{-webkit-transform: scaleY(0.6) translateY(3px);transform: scaleY(0.6) translateY(3px)}
  80%{-webkit-transform: scaleY(1) translateY(0);transform: scaleY(1) translateY(0);}
  100%{-webkit-transform: scaleY(1) translateY(0);transform: scaleY(1) translateY(0);}
}

@keyframes bounce {
  0%{-webkit-transform: scaleY(1) translateY(0);transform: scaleY(1) translateY(0);}
  10%{-webkit-transform: scaleY(0.3) translateY(3px);transform: scaleY(0.3) translateY(3px);}
  30%{-webkit-transform: scaleY(1);transform: scaleY(1)}
  30%{-webkit-transform: scaleY(1.3) translateY(-16px);transform: scaleY(1.3) translateY(-16px);}
  60%{-webkit-transform: scaleY(0.6) translateY(3px);transform: scaleY(0.6) translateY(3px)}
  80%{-webkit-transform: scaleY(1) translateY(0);transform: scaleY(1) translateY(0);}
  100%{-webkit-transform: scaleY(1) translateY(0);transform: scaleY(1) translateY(0);}
}



