.work-list{
  -ms-flex: 0 0 100%;
      flex: 0 0 100%;
  overflow-y: scroll;
  height: 100%;
  padding-top: 100%;
  opacity: 0;
  -webkit-animation: work-list-slide-in 800ms ease-out 2000ms;
          animation: work-list-slide-in 800ms ease-out 2000ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes work-list-slide-in{
  100%{padding-top: 0; opacity: 1;}
}

@keyframes work-list-slide-in{
  100%{padding-top: 0; opacity: 1;}
}



